<template lang="html">
  <div class="messages">
    <nav-header></nav-header>
    <div class="headpic">
      <div class="container headtitle full">
        <div class="title">
          <h1 href="/blog">{{this.user.nickname}}</h1>
        </div>
      </div>
    </div>
    <div class="container full">
      <div class="list">
        <div>
          <div class="full-content">
            <div class="messages-text post-content">
              <div style="overflow:hidden;margin-bottom:20px;">
                <h3 class="hestia-title text-center">请留下你的意见或者描述使用中遇到的问题</h3>
<!--                <textarea ref='textBox' spellcheck='false' row="1" placeholder="咱们交♂流交♂流~~" v-model="message" class="msg-content" cols="45" rows="8" aria-required="true"></textarea>-->
                <el-input style="font-size: 20px" type="textarea" :rows="5" placeholder="请输入文本" v-model="message">
                </el-input>
                <br>
                <button @click='submit' :disabled='submitFlag' class="submit">
                      <span>{{submitFlag ? '提交中...' : '发布评论'}}</span>
                </button>
              </div>
              <div class="msg" v-if="isShow">
                <Bug :bugs="messagesList"></Bug>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <scroll-top></scroll-top>
    <nav-footer></nav-footer>
  </div>
</template>

<script>
import NavHeader from '@/components/Header.vue'
import NavFooter from '@/components/Footer.vue'
import scrollTop from '@/components/scrollTop.vue'
import Bug from "@/components/Bug"
export default {
  data () {
    return {
      messagesList: [],
      message: '',
      status: '请输入',
      submitFlag: false,
      isShow:false,
      user:{},
    }
  },
  components: {
    NavHeader,
    NavFooter,
    scrollTop,
    Bug,
  },
  created(){
    this.getBugs()
    this.getUserInfo()
  },
  mounted () {
  },
  methods: {

    getUserInfo(){
      let username = window.sessionStorage.getItem("user")
      if(username == null){
        window.sessionStorage.removeItem("user")
        this.$router.push("/")
        return ;
      }
      let url = "/user/?username=" + username
      this.getRequest(url).then(resp => {
        if(resp) {
          this.user = resp
        }
      })
    },

    getBugs () {
      let url = '/bug/all'
      this.getRequest(url).then(resp => {
        this.messagesList = resp.data
        this.isShow = true
      })
    },

    submit () {
      let reg = /^[\w_-]+@[\w_-]+\.[\w\\.]+$/
      if (!this.message) {
        this.status = '是不是忘了点什么'
        this.Notify(this.status)
        return
      } else if (this.message.length > 500) {
        this.status = '可不能这么长哦'
        this.Notify(this.status)
        return
      } else if (/\d{7,}/i.test(this.message) || // 连续7个以上数字，过滤发Q号和Q群的评论
                /(\d.*){7,}/i.test(this.message) || // 非连续的7个以上数字，过滤用字符间隔开的Q号和Q群的评论
                /\u52A0.*\u7FA4/i.test(this.message) || // 包含“加群”两字的通常是发Q群的垃圾评论
                /(\u9876.*){5,}/i.test(this.message) || // 过滤5个以上“顶”字的评论
                /([\u4E00\u4E8C\u4E09\u56DB\u4E94\u516D\u4E03\u516B\u4E5D\u25CB\u58F9\u8D30\u53C1\u8086\u4F0D\u9646\u67D2\u634C\u7396\u96F6].*){7,}/i.test(this.message) // 过滤用汉字发Q号和Q群的评论
      ) {
        this.status = '可不能发不好的东东哦'
        this.Notify(this.status)
        return
      }
      this.submitFlag = true
      let url = "/bug/add"
      let param = {
        'content':this.message,
        'author':this.user.nickname,
        'profile':this.user.profile,
      }
      this.postRequest(url, param).then(resp => {
        if(resp){
          this.submitFlag = false
        }
      })
    },

    Notify(statue){
      this.$notify({
        title: '警告',
        message: statue,
        type: 'warning',
      })
      return
    },
  }
}
</script>

