        <el-card class="box-card" v-for = "(item,index) in bugList" :key="index">
            <div slot="header" class="clearfix">
                <div style="vertical-align: center;overflow: hidden">
                    <div style="float:left">
                        <el-avatar :fit="'fill'" :src="imgUrl + item.profile" :key="imgUrl + item.profile" alt="头像"></el-avatar>
                    </div>
                    <div style="float:left;horiz-align: center">
                        <div style="margin-left: 20px">
                            {{item.author}}
                        </div>
                    </div>
                </div>
            </div>
            <div class="text">{{item.content}}</div>
            <hr>
            <div>
                <div style="float: left">
                    <p>{{item.createTime}}</p>
                </div>
                <div class="ui label red" style="float: right">
